/* podstawowe resety */

/* importy fontow */

@font-face {
  font-family: 'Roboto';
  src: url('../assets/fonts/RobotoDefault.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

/* colour variables */
:root {
  --primary-color: rgb(210, 161, 210);
  --secondary-color: #000103; 
}

/* Podstawowy reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Ustawienia dla body i fontów */
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
  background-color: var(--secondary-color);
  color: #000;
}

/* Reset nagłówków */
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

/* Reset list */
ul, ol {
  list-style: none;
}

/* Reset linków */
a {
  text-decoration: none;
  color: inherit;
}

/* Reset obrazków */
img {
  max-width: 100%;
  display: block;
}

/* dobra, teraz powazny kod okej thx */

/* Heading component */

.heading {
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000;
}

.heading button::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -15px; /* odległość od tekstu */
    width: 100%;
    height: 2px; /* grubość linii */
    background-color: var(--primary-color);
}

.nav-buttons button {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 15px;
    margin-left: 100px;
    cursor: pointer;
    background-color: transparent;
    border: 0px;
    color: var(--primary-color);
    position: relative;       /* ważne! */
}

.heading_h1 {
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    font-size: 35px;
    color: var(--primary-color);
}

/* Hero component */

.heroDiv {
    position: relative;
    z-index: 1;
    color: white;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
}

.heroDiv button {
    cursor: pointer;
    background: linear-gradient(to bottom, #6366f1, #4f46e5); /* indigo gradient */
    box-shadow: 0px 4px 32px rgba(99, 102, 241, 0.7);
    padding: 0.75rem 1.5rem; /* py-3 px-6 */
    border-radius: 1rem; /* rounded-xl */
    border: 1px solid #94a3b8; /* slate-500 */
    color: white;
    font-weight: 500; /* medium */
    font-family: inherit;
    height: 50px;
    width: 150px;
    margin-top: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    overflow: hidden;
}

.heroDiv button:hover {
    transform: scale(1.1) !important;
}

.heroDiv button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  transform: skewX(-20deg);
  transition: left 0.5s;
}

.heroDiv button:hover::before {
  left: 100%;
}

.heroDivButtonAddIfVisible {
   transition: transform 0.3s ease !important;
}

.heroDiv h1 {
    font-size: 60px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
}

.heroDiv p {
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    margin-top: 20px;
}

.hrLine {
    background-color: #000000;
    border: none;
    height: 2px;
}

