}

.description_p {
  text-wrap: wrap;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  padding-bottom: 20px;
  color: white;
  
}

/* About Me component */

.AboutMe {
  color: white;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.AboutMe h1 {
  color: var(--primary-color);
  padding-bottom: 50px;
  font-size: 50px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
}

.AboutMe p {
  text-wrap: wrap;
  padding-bottom: 50px;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
}

.AboutMeCardsDiv {
  display: flex;
  gap: 30px;
}

.AboutMeCard {
  background: rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 30px;
  color: white;
