  height: 175px;
  transition: transform 0.3s ease, background 0.3s ease !important;
}

.AboutMeCard:hover {
  background: rgba(255, 255, 255, 0.2) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transform: scale(1.05) !important;
  transition: transform 0.3s ease !important;
}

.AboutMeCard h2 {
  color: var(--primary-color);
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin-bottom: 10px;
  font-size: 35px;
}


.description h1 {
    color: var(--primary-color);
    padding-bottom: 50px;
    font-size: 50px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    padding-top: 200px;
}

/* Technologies component */

.MySkills {
  color: white;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
  padding-bottom: 100px;
  padding-top: 150px;
}

.MySkills h1 {
  color: var(--primary-color);
  padding-bottom: 50px;
  font-size: 50px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
}

.MySkills p {
  text-wrap: wrap;
  padding-bottom: 50px;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
}

.skill-card {
  background: rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 30px;
  color: white;
  width: 200px;
  height: 200px;
  transition: transform 0.3s ease, background 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skill-card:hover {
  background: rgba(255, 255, 255, 0.2) !important;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transform: scale(1.05) !important;
  transition: transform 0.3s ease !important;
}

.skill-card img {
  width: 64px;
  height: 64px;
  object-fit: contain;
  margin-bottom: 10px;
  filter: invert(1)
}

.MySkillsLayout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px; /* smaller, nicer gap */
  width: 100%;
  justify-items: center;
  align-items: start; /* optional, aligns cards to the top */
  padding: 10px; /* optional, adds some spacing around the grid */
}

.fancy-button {
  cursor: pointer;
  background: linear-gradient(to bottom, #6366f1, #4f46e5); /* indigo gradient */
  box-shadow: 0px 4px 32px rgba(99, 102, 241, 0.7);
  padding: 0.75rem 1.5rem; /* py-3 px-6 */
  border-radius: 1rem; /* rounded-xl */
  border: 1px solid #94a3b8; /* slate-500 */
  color: white;
  font-weight: 500; /* medium */
  position: relative;
  font-family: inherit;
  height: 50px;
  width: 150px;
  margin-top: 75px;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  transition: transform 0.3s ease;
  overflow: hidden;
}

.fancy-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  transform: skewX(-20deg);
  transition: left 0.5s;
}

.fancy-button:hover::before {
  left: 100%;
}

.fancy-button:hover {
  transform: scale(1.2) !important;
}

/* Animations for About Me section */

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.aboutme-fade {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s, transform 1s;
}

.aboutme-fade.animate {
  opacity: 1;
  transform: translateY(0);
}

.aboutme-fade.delay-1.animate {
  transition-delay: 0.2s;
}
.aboutme-fade.delay-2.animate {
  transition-delay: 0.6s;
}

.aboutme-fade.delay-3.animate {
  transition-delay: 0.8s;
}

.aboutme-fade.delay-4.animate {
  transition-delay: 1s;
}

.aboutme-fade.delay-5.animate {
  transition-delay: 1.2s;
}

.aboutme-fade.delay-6.animate {
  transition-delay: 1.4s;
}

.aboutme-fade.delay-7.animate {
  transition-delay: 1.6s;
}


.aboutme-fade-reversed {
  opacity: 0;
  transform: translateY(-30px);
  transition: opacity 1s, transform 1s;
}

.aboutme-fade-reversed.animate {
  opacity: 1;
  transform: translateY(0);
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* footer component */

.footer {
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    padding: 20px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    bottom: 0;
    width: 100%;
    margin-top: 50px;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
}
.footer a {
    color: var(--primary-color);
    text-decoration: none;
}
.footer a:hover {
    text-decoration: underline;
}


/* footer component */ 

/* Futuristic Footer */
.footer-futuristic {
    position: relative;
    width: 100%;
    padding: 24px 0;
    margin-top: 80px;
    font-family: 'Roboto', sans-serif;
    background: none;
    color: inherit;
    box-shadow: none;
    overflow: visible;
}

.footer-glow {
    position: absolute;
    top: -60px;
    left: 50%;
    transform: translateX(-50%);
    width: 600px;
    height: 120px;
    opacity: 0.25;
    filter: blur(32px);
    z-index: 0;
    pointer-events: none;
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 32px;
    gap: 40px;
    position: relative;
}

.footer-logo {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 16px;
    font-family: 'Roboto', sans-serif;
    color: inherit;
    text-shadow: none;
    letter-spacing: 1px;
}

.footer-brand {
    flex: 1 1 220px;
    padding-left: 0;
    border-left: none;
}

.footer-location, .footer-email {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1rem;
    margin: 10px 0;
    color: inherit;
    letter-spacing: 0;
}

.footer-email a {
    color: inherit;
    text-decoration: none;
    font-weight: 500;
    transition: none;
}

.footer-social {
    display: flex;
    gap: 22px;
    align-items: center;
    margin: 18px 0;
}

.footer-icon {
    width: 28px;
    height: 28px;
    filter: none;
    transition: none;
}

.footer-message {
    flex: 2 1 320px;
    margin-top: 8px;
    background: none;
    border-radius: 0;
    padding: 0;
    box-shadow: none;
}

.footer-message h3 {
    margin-bottom: 12px;
    font-size: 1.1rem;
    color: inherit;
    letter-spacing: 0;
    text-shadow: none;
}

.footer-message p {
    font-size: 1rem;
    color: inherit;
    letter-spacing: 0;
}

.footer-bottom {
    text-align: center;
    margin-top: 40px;
    font-size: 1rem;
    color: inherit;
    letter-spacing: 0;
    position: relative;
}